<template>
  <v-card>
    <v-card-text class="px-7">
      <showDataAccessHeader @add='add'
                            :label="$t('查看下属人员数据')"
                            :readonly='readonly'
                            v-model="dataobj.mrbase_sys_data_strategy_subordinate_is_used" />
      <!-- <tree v-model="list."></tree> -->
      <dialogModule v-model="open" />
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       v-if="!readonly"
                       :cancelText="$store.state.uuid?$t('取消'):$t('label_The_previous_step')"
                       @cancel='cancel'></mrc_ess_submitBtn>
  </v-card>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import {
  listData,
  getData,
  updateData,
  addData,
  delData
} from '@/service/api/data-access-strategy'
import showDataAccessHeader from '../showDataAccessHeader'
import dialogModule from './dialogModule'
export default {
  components: { showDataAccessHeader, dialogModule },
  props: {
    readonly: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      open: false,
      dataobj: {
        mrbase_sys_data_strategy_subordinate_is_used: '0'
      },
      list: [],
    };
  },
  validations: {},
  computed: {},
  created () { },
  watch: {},
  async mounted () {
    if (this.$store.state.uuid) {//修改
      await this.getList()
    }
    else {//新增
    }
  },

  methods: {
    async getList () {
      if (this.$store.state.uuid) {
        const res = await getData({ org_level_uuid: this.$store.state.uuid, date: 'mrbase_sys_data_strategy_subordinate' })
        if (res.code == 200) {
          this.list = res.mrbase_sys_data_strategy_same_post_grade
          this.dataobj = res.data
        }
      }
      else {

      }
    },
    add () {
      this.open = true
    },
    success () {
      if (this.$store.state.uuid) {//修改
        this.$emit('success')
      }
      else {//新增
        this.$emit('success')
      }
    },
    cancel () {
      this.$emit('cancel')
    }
  },
};
</script>

